getLatestValue(){
console.log('test1');
    this._angularFireStore
      .collection("vehicles", (ref)=>{
        return ref.where("id", ">", 0).orderBy("id", 'desc').limit(1)
      }).snapshotChanges().subscribe(data => {

    this.vehicleList = data.map(e => {
          return {
            id: e.payload.doc.data()['id'],
          };
        })
    console.log("id",typeof this.vehicleList[0].id);
    this.counter = parseInt(this.vehicleList[0].id)+1;


      });

  //  console.log("lastestval", this._angularFireStore.collection('vehicles').doc(id).ref.get());
  }
