import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http"
import { Observable } from 'rxjs';
import { Activity } from './types';

@Injectable({
  providedIn: 'root'
})
export class ActivityService {
  settings = {
  "async": true,
  "crossDomain": true,
  "url": "https://api.msg91.com/api/sendhttp.php?mobiles=919966330990&authkey=258429At8ExWrCdC15c4c2a82&route=4&sender=TESTIN&message=Hello! This is a test message&country=91",
  "method": "GET",
  "headers": {}
}

  constructor(private _httpClient: HttpClient) { }


  getActivity(activityID: string): Observable<Activity>{
    return this._httpClient.get<Activity>(API + "/id/" + activityID);
  }

  getAllActivites(): Observable<Activity[]>{
    return this._httpClient.get<Activity[]>(API);
  }

  sendSMS(): Observable<Activity[]>{
    return this._httpClient.get<Activity[]>(API);
  }
}

const API = "https://orangevalleycaa.org/api/videos";
